.main-container {
  background-image: url("../../Carcassonne.jpg");
  height: 1000px;
  background-size: cover;
  
  .home-header {
    color: $secondary-accent-color;
    font-size: 50px;
    margin: 30px 0 60px 30px;
    padding-top: 30px;
    width: 40%;
  }

  .button-container {
    box-sizing: border-box;
    width: 40%;
    margin-left: 50%;
    button {
      border: 3px solid $accent-color;
      border-radius: 5px;
      color: $accent-color;
      font-weight: 500;
      background-color: $secondary-main-color;
      p {
        color: $accent-color;
        font-weight: 500;
        font-size: 40px;
        width: 400px;
      }
  
  }
  
  }

  


  

